import { useState } from "react";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";
import Row from "react-bootstrap/Row";
import { Link } from "react-router-dom";
import { GetStartedButton, PasswordCol, SignUpFormCont } from "./styled";

const SignUpForm = () => {
    const [hidePasswordField, sethidePasswordField] = useState(true);
    const onEmailPress = () => hidePasswordField && sethidePasswordField(false);

    return (
        <SignUpFormCont>
            <form method="get">
                <Form.Group>
                    <Row>
                        <Col md={hidePasswordField ? 3 : 2} />
                        <Col md={3}>
                            <Form.Control
                                type="email"
                                required={true}
                                placeholder="Email"
                                name="email"
                                size="sm"
                                onKeyPress={onEmailPress}
                                onChange={onEmailPress}
                            />
                        </Col>

                        <PasswordCol md={3} isHidden={hidePasswordField}>
                            <Form.Control
                                type="password"
                                required={true}
                                placeholder="Password"
                                size="sm"
                                name="password"
                            />
                        </PasswordCol>
                        <Col md={3}>
                            <Link to={`/dashboard`}>
                                <GetStartedButton variant="primary" size="md">
                                    Signup with Email!
                                </GetStartedButton>
                            </Link>
                        </Col>
                    </Row>
                </Form.Group>
            </form>
        </SignUpFormCont>
    );
};

export default SignUpForm;
