import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/esm/Col";
import styled from "styled-components";

export const GetStartedButton = styled(Button)`	
    min-width: 300px	
    text-align: center;	
`;

export const SignUpFormCont = styled.div`
    text-align: left;
`;

const getPasswordColStyles = ({ isHidden }: { isHidden: boolean }) => `	
    max-width: ${isHidden ? "0" : "100%"};	
    padding: ${isHidden ? "0" : "0 15px 0 15px"};	
    input {	
        display: ${isHidden ? "none" : "inline"};	
    }	
`;

export const PasswordCol = styled(Col)`
    transition: all 1.5s ease;
    ${getPasswordColStyles}
`;
